import barTitle from '@/components/zaiui-common/basics/bar-title';
import _goods_data from '@/static/zaiui/data/goods.js';	//虚拟数据
import _tool from '@/utils/tools.js';	//工具函数
import luPopupWrapper from "@/components/lu-popup-wrapper/lu-popup-wrapper.vue";
export default {
	components: {
		barTitle,
		luPopupWrapper
	},
	data() {
		return {
			indicatorDots: false,
			autoplay: true,
			interval: 2000,
			duration: 500,
			circular:true,
			bannerCur: 0, bannerList: [], bottomModal: false, modalTitle: 'loading...', modalType: 'promotion', selectType: 'loading...',
			goodsList: [],
			modalObj:{
				type:"bottom",// left right top bottom center
				transition:"slider",//none slider fade
				backgroundColor:'#fff',
				active:false,
				height:"680upx",
				width:"100%",
				popupId:1,
				maskShow:true,
				maskClick:true
			}
		}
	},
	onLoad() {
		this.bannerList = _goods_data.bannerListData();
		this.goodsList = _goods_data.goodsList();
	},
	onReady() {
		_tool.setBarColor(true);
		uni.pageScrollTo({
		    scrollTop: 0,
		    duration: 0
		});
	},
	methods: {
		// 弹窗关闭回调
		closeCallback:function() {
			console.log("关闭后回调");
		},
		bannerSwiper(e) {
			this.bannerCur = e.detail.current;
		},
		serviceTap() {
			this.modalTitle = "服务";
			this.modalType = 'service';
			this.modalObj.width ="100%";
			this.modalObj.height ="900upx";
			this.modalObj.transition = "slider";
			this.modalObj.type = "bottom";
			this.showMyModal();
		},
		promotionTap() {
				this.modalTitle = "促销优惠";
				this.modalType = 'promotion';
				this.showMyModal();
		},
		selectTap(type) {
			this.selectType = type;
			this.modalTitle = "选择规格";
			this.modalType = 'select';
			this.showMyModal();
		},
		showMyModal() {
			// this.bottomModal = true;
			this.$refs.luPopupWrapper.show(true);
		},
		hideMyModal(e) {
			// this.bottomModal = false;
			// this.modalTitle = "";
			// this.modalType = '';
			this.$refs.luPopupWrapper.close(true);
		},
		myCartTap(){
			uni.navigateTo({
				url: '/pages/goods/my_cart'
			});
		}
	}
}
